// Utilit√°rios globais
function round2(n) {
  return Math.round((n + Number.EPSILON) * 100) / 100
}

function formatNumber(n) {
  return n.toLocaleString('pt-BR', { maximumFractionDigits: 2, minimumFractionDigits: 0 })
}

function getRegra(medicamento, especie) {
  const mapa = DOSAGENS[medicamento]
  if (!mapa) return null
  return mapa[especie] || mapa['cachorro'] || mapa['gato'] || null
}

// Fun√ß√£o para parsear dose dos JSONs (ex: "C√£es e Gatos - 11 a 22mg/kg" ou "C√£es - 5 a 10mg/kg Gato - 0,5 a 2mg/kg" ou "2 - 4- 8mg/kg")
function parseDoseFromJSON(doseStr, especie) {
  if (!doseStr) return null
  
  const especieMap = { 
    'cachorro': ['C√£es', 'C√£o', 'C√£o -', 'C√£es -'], 
    'gato': ['Gatos', 'Gato', 'Gato -', 'Gatos -'],
    'coelho': ['Coelho', 'Coelhos'],
    'cavalo': ['Cavalo', 'Cavalos'],
    'ave': ['Ave', 'Aves'],
    'boi': ['Boi', 'Bois'],
    'porco': ['Porco', 'Porcos']
  }
  const especieTerms = especieMap[especie] || []
  
  // Primeiro, tentar encontrar dose espec√≠fica para a esp√©cie (ex: "C√£es - 0,5 a 1mg/kg Gato - 0,5 a 2mg/kg")
  if (especieTerms.length > 0) {
    for (const term of especieTerms) {
      const especieRegex = new RegExp(`${term.replace(/[.*+?^${}()|[\]\\]/g, '\\$&')}\\s*([^Gato|C√£o|Gatos|C√£es]*?)(?:Gato|C√£o|Gatos|C√£es|$)`, 'i')
      const especieMatch = doseStr.match(especieRegex)
      if (especieMatch && especieMatch[1]) {
        const parteEspecie = especieMatch[1]
        // Procurar faixa na parte espec√≠fica
        const faixaRegex = /(\d+(?:[.,]\d+)?)\s*(?:a|at√©|-)\s*(\d+(?:[.,]\d+)?)\s*mg\/kg/i
        const faixaMatch = parteEspecie.match(faixaRegex)
        if (faixaMatch) {
          const min = parseFloat(faixaMatch[1].replace(',', '.'))
          const max = parseFloat(faixaMatch[2].replace(',', '.'))
          return { min, max }
        }
        // Tentar dose √∫nica na parte espec√≠fica
        const singleRegex = /(\d+(?:[.,]\d+)?)\s*mg\/kg/i
        const singleMatch = parteEspecie.match(singleRegex)
        if (singleMatch) {
          const val = parseFloat(singleMatch[1].replace(',', '.'))
          return { min: val, max: val }
        }
      }
    }
  }
  
  // Se n√£o encontrou espec√≠fico, procurar padr√µes gerais com m√∫ltiplos valores (ex: "2 - 4- 8mg/kg" ou "15 - 22 - 30mg/kg")
  const multiRegex = /(\d+(?:[.,]\d+)?)\s*(?:-|a|at√©)\s*(\d+(?:[.,]\d+)?)\s*(?:-|a|at√©)?\s*(\d+(?:[.,]\d+)?)?\s*mg\/kg/i
  const multiMatch = doseStr.match(multiRegex)
  
  if (multiMatch) {
    const values = []
    if (multiMatch[1]) values.push(parseFloat(multiMatch[1].replace(',', '.')))
    if (multiMatch[2]) values.push(parseFloat(multiMatch[2].replace(',', '.')))
    if (multiMatch[3]) values.push(parseFloat(multiMatch[3].replace(',', '.')))
    
    if (values.length > 0) {
      const min = Math.min(...values)
      const max = Math.max(...values)
      return { min, max }
    }
  }
  
  // Procurar por padr√µes como "11 a 22mg/kg" ou "5 a 10mg/kg"
  const regex = /(\d+(?:[.,]\d+)?)\s*(?:a|at√©|-)\s*(\d+(?:[.,]\d+)?)\s*mg\/kg/i
  const match = doseStr.match(regex)
  
  if (match) {
    const min = parseFloat(match[1].replace(',', '.'))
    const max = parseFloat(match[2].replace(',', '.'))
    return { min, max }
  }
  
  // Tentar dose √∫nica (ex: "5mg/kg")
  const singleRegex = /(\d+(?:[.,]\d+)?)\s*mg\/kg/i
  const singleMatch = doseStr.match(singleRegex)
  if (singleMatch) {
    const val = parseFloat(singleMatch[1].replace(',', '.'))
    return { min: val, max: val }
  }
  
  return null
}

function renderForm(initial = {}) {
  const panel = document.getElementById('panel')
  const especie = initial.especie || 'cachorro'
  const pesoStr = initial.pesoStr || ''
  const categoria = initial.categoria || ''
  const med = initial.med || ''
  const concStr = initial.concStr || ''
  const sensivel = Boolean(initial.sensivel)

  // Gerar op√ß√µes de categorias
  const categoriasOptions = Object.entries(CATEGORIAS).map(([key, cat]) => 
    `<option value="${key}" ${categoria===key?'selected':''}>${cat.nome}</option>`
  ).join('')

  panel.innerHTML = `
      <h2 class="text-lg font-semibold text-gray-900">Calcular dosagem</h2>
      <p class="mt-1 text-sm text-gray-600">Selecione a categoria, esp√©cie, informe o peso, o medicamento e, opcionalmente, a concentra√ß√£o.</p>

      <form id="dose-form" class="mt-6 grid grid-cols-1 gap-4 sm:grid-cols-2">
        <div class="sm:col-span-2">
          <label for="animal" class="block text-sm font-medium text-gray-700">Esp√©cie</label>
          <select id="animal" class="mt-1 w-full rounded-md border-gray-300 bg-white px-3 py-2 text-gray-900 shadow-sm focus:border-pink-500 focus:outline-none focus:ring-2 focus:ring-pink-500/20">
            ${ESPECIES.map(s => `<option value="${s.value}" ${s.value===especie?'selected':''}>${s.label}</option>`).join('')}
          </select>
        </div>

        <div>
          <label for="categoria" class="block text-sm font-medium text-gray-700">Categoria</label>
          <select id="categoria" class="mt-1 w-full rounded-md border-gray-300 bg-white px-3 py-2 text-gray-900 shadow-sm focus:border-pink-500 focus:outline-none focus:ring-2 focus:ring-pink-500/20">
            <option value="">Selecione uma categoria</option>
            ${categoriasOptions}
          </select>
        </div>

        <div>
          <label for="medicamento" class="block text-sm font-medium text-gray-700">Medicamento</label>
          <select id="medicamento" class="mt-1 w-full rounded-md border-gray-300 bg-white px-3 py-2 text-gray-900 shadow-sm focus:border-pink-500 focus:outline-none focus:ring-2 focus:ring-pink-500/20" ${!categoria ? 'disabled' : ''}>
            <option value="">Selecione uma categoria</option>
          </select>
        </div>

        <div>
          <label for="peso" class="block text-sm font-medium text-gray-700">Peso <span class="text-gray-400 font-normal">(kg)</span></label>
          <div class="relative mt-1">
            <span class="pointer-events-none absolute inset-y-0 left-3 flex items-center text-gray-500">kg</span>
            <input id="peso" inputmode="numeric" autocomplete="off" placeholder="0,00" value="${pesoStr}"
              aria-label="Peso do animal em quilogramas"
              class="w-full rounded-md border-gray-300 pl-10 pr-3 py-2 text-gray-900 shadow-sm focus:border-pink-500 focus:outline-none focus:ring-2 focus:ring-pink-500/20" />
          </div>
        </div>

        <div>
          <label for="concentracao" class="block text-sm font-medium text-gray-700">Concentra√ß√£o <span class="text-gray-400 font-normal">(mg/mL)</span></label>
          <div id="concentracao-container" class="mt-1">
            <input id="concentracao" inputmode="decimal" placeholder="Ex.: 250" value="${concStr}" 
              aria-label="Concentra√ß√£o do medicamento em miligramas por mililitro"
              class="w-full rounded-md border-gray-300 px-3 py-2 text-gray-900 shadow-sm focus:border-pink-500 focus:outline-none focus:ring-2 focus:ring-pink-500/20" />
            <div id="concentracao-opcoes" class="mt-2 hidden flex flex-wrap gap-2" role="group" aria-label="Concentra√ß√µes comuns dispon√≠veis"></div>
          </div>
        </div>

        <div class="flex items-center gap-2 sm:col-span-2">
          <input id="racaSensivel" type="checkbox" ${sensivel?'checked':''} class="h-4 w-4 rounded border-gray-300 text-pink-600 focus:ring-pink-600" />
          <label for="racaSensivel" class="text-sm text-gray-700">Ra√ßa sens√≠vel (Collie/Sheltie) ‚Äî alerta para Ivermectina</label>
        </div>

        <div class="sm:col-span-2 mt-2">
          <button type="submit" aria-label="Calcular dosagem do medicamento" class="inline-flex w-full items-center justify-center rounded-md bg-pink-500 px-4 py-2 text-white font-medium shadow-sm transition hover:bg-pink-600 focus:outline-none focus:ring-2 focus:ring-pink-500/30 cursor-pointer">Calcular dose</button>
        </div>
      </form>

      <div id="alert" class="mt-4 hidden rounded-md border border-red-200 bg-red-50 px-4 py-3 text-sm text-red-700"></div>
  `

  const form = document.getElementById('dose-form')
  const categoriaSelect = document.getElementById('categoria')
  const especieSelect = document.getElementById('animal')
  const pesoInput = document.getElementById('peso')
  const medicamentoSelect = document.getElementById('medicamento')
  const concentracaoInput = document.getElementById('concentracao')
  const racaSensivel = document.getElementById('racaSensivel')
  const alertBox = document.getElementById('alert')

  // Fun√ß√£o para extrair concentra√ß√£o do JSON (ex: "500mg/mL" ou "Comprimidos 50mg")
  function extrairConcentracao(concStr) {
    if (!concStr) return null
    // Procurar por padr√µes como "500mg/mL", "50mg/mL", "250 mg/5 mL"
    const regex = /(\d+(?:[.,]\d+)?)\s*mg\s*\/\s*(\d+(?:[.,]\d+)?)?\s*mL/i
    const match = concStr.match(regex)
    if (match) {
      const mg = parseFloat(match[1].replace(',', '.'))
      const ml = match[2] ? parseFloat(match[2].replace(',', '.')) : 1
      return round2(mg / ml) // Retorna mg/mL
    }
    // Procurar por comprimidos (ex: "Comprimidos 50mg")
    const compRegex = /(?:comp|comprimido|cp)[\s:]*(\d+(?:[.,]\d+)?)\s*mg/i
    const compMatch = concStr.match(compRegex)
    if (compMatch) {
      return parseFloat(compMatch[1].replace(',', '.'))
    }
    return null
  }

  // Fun√ß√£o para detectar se √© medica√ß√£o l√≠quida baseado na via
  function isLiquido(via) {
    if (!via) return false
    const viasLiquidas = ['IV', 'IM', 'SC', 'VO', 'INJET√ÅVEL', 'INJETAVEL', 'SOLU√á√ÉO', 'SUSPENS√ÉO', 'SUSPENSAO']
    return viasLiquidas.some(v => via.toUpperCase().includes(v))
  }

  // Fun√ß√£o para obter concentra√ß√µes padr√£o de um medicamento
  function getConcentracoesPadrao(nomeMed) {
    if (!nomeMed) return null
    const nomeLower = nomeMed.toLowerCase()
    // Procurar por nome exato ou parcial
    for (const [key, valores] of Object.entries(CONCENTRACOES_PADRAO)) {
      if (nomeLower.includes(key)) {
        return valores
      }
    }
    return null
  }

  // Fun√ß√£o para atualizar medicamentos baseado na categoria
  function atualizarMedicamentos() {
    const categoriaKey = categoriaSelect.value
    medicamentoSelect.innerHTML = '<option value="">Selecione uma categoria</option>'
    medicamentoSelect.disabled = !categoriaKey
    concentracaoInput.value = ''

    if (categoriaKey && CATEGORIAS[categoriaKey]) {
      const medicamentos = CATEGORIAS[categoriaKey].dados
      medicamentos.forEach((med, idx) => {
        const nome = med.Medicamento || med.medicamento || `Medicamento ${idx + 1}`
        const value = nome.toLowerCase().replace(/[^a-z0-9]+/g, '-')
        medicamentoSelect.innerHTML += `<option value="${value}" data-med-data='${JSON.stringify(med)}'>${nome}</option>`
      })
    }
  }

  // Quando medicamento for selecionado, preencher concentra√ß√£o automaticamente APENAS se estiver no JSON
  medicamentoSelect.addEventListener('change', () => {
    const medOption = medicamentoSelect.options[medicamentoSelect.selectedIndex]
    const opcoesContainer = document.getElementById('concentracao-opcoes')
    opcoesContainer.innerHTML = ''
    opcoesContainer.classList.add('hidden')
    
    // Limpar campo de concentra√ß√£o
    concentracaoInput.value = ''
    
    if (medOption?.dataset?.medData) {
      const medData = JSON.parse(medOption.dataset.medData)
      const nomeMed = medData.Medicamento || ''
      const via = medData.Via || ''
      
      // Tentar extrair do JSON - S√ì preencher se encontrar informa√ß√£o v√°lida
      if (medData.Concentracao_mg_mL) {
        const concExtraida = extrairConcentracao(medData.Concentracao_mg_mL)
        if (concExtraida && concExtraida > 0) {
          // S√≥ preencher se conseguiu extrair uma concentra√ß√£o v√°lida do JSON
          concentracaoInput.value = formatNumber(concExtraida)
        } else {
          // Se n√£o conseguiu extrair, mas √© l√≠quido, mostrar op√ß√µes como sugest√£o (SEM preencher)
          if (isLiquido(via)) {
            const concPadrao = getConcentracoesPadrao(nomeMed)
            if (concPadrao && concPadrao.length > 0) {
              opcoesContainer.classList.remove('hidden')
              opcoesContainer.innerHTML = '<span class="text-xs text-gray-500 mr-2">Concentra√ß√µes comuns (clique para selecionar):</span>'
              concPadrao.forEach(conc => {
                const btn = document.createElement('button')
                btn.type = 'button'
                btn.className = 'px-3 py-1 text-xs rounded-md bg-pink-100 text-pink-700 hover:bg-pink-200 transition cursor-pointer'
                btn.textContent = `${formatNumber(conc)} mg/mL`
                btn.onclick = () => {
                  concentracaoInput.value = formatNumber(conc)
                  opcoesContainer.classList.add('hidden')
                }
                opcoesContainer.appendChild(btn)
              })
              // N√ÉO preencher automaticamente - apenas mostrar op√ß√µes
            }
          }
        }
      } else {
        // Se n√£o tem concentra√ß√£o no JSON e √© l√≠quido, mostrar op√ß√µes como sugest√£o (SEM preencher)
        if (isLiquido(via)) {
          const concPadrao = getConcentracoesPadrao(nomeMed)
          if (concPadrao && concPadrao.length > 0) {
            opcoesContainer.classList.remove('hidden')
            opcoesContainer.innerHTML = '<span class="text-xs text-gray-500 mr-2">Concentra√ß√µes comuns (clique para selecionar):</span>'
            concPadrao.forEach(conc => {
              const btn = document.createElement('button')
              btn.type = 'button'
              btn.className = 'px-3 py-1 text-xs rounded-md bg-pink-100 text-pink-700 hover:bg-pink-200 transition cursor-pointer'
              btn.textContent = `${formatNumber(conc)} mg/mL`
              btn.onclick = () => {
                concentracaoInput.value = formatNumber(conc)
                opcoesContainer.classList.add('hidden')
              }
              opcoesContainer.appendChild(btn)
            })
            // N√ÉO preencher automaticamente - apenas mostrar op√ß√µes
          }
        }
      }
    }
  })

  // Se j√° tinha categoria selecionada, atualizar medicamentos
  if (categoria) {
    atualizarMedicamentos()
    if (med) {
      medicamentoSelect.value = med
    }
  }

  categoriaSelect.addEventListener('change', atualizarMedicamentos)

  function maskDecimal(el) {
    el.addEventListener('input', () => {
      let s = el.value
      s = s.replace(/[^\d,\.]/g, '')
      s = s.replace(/\./g, '')
      if (s.indexOf(',') !== -1) {
        const [int, dec] = s.split(',')
        const intFmt = int.replace(/\B(?=(\d{3})+(?!\d))/g, '.')
        el.value = dec !== undefined ? `${intFmt},${dec.replace(/[^\d]/g,'').slice(0,2)}` : intFmt
      } else {
        const intFmt = s.replace(/\B(?=(\d{3})+(?!\d))/g, '.')
        el.value = intFmt
      }
    })
  }

  // M√°scara de 2 casas decimais autom√°tica (ex.: 1250 -> 12,50)
  function maskTwoDecimals(el) {
    const format = (digits) => {
      if (!digits) return ''
      // remove zeros √† esquerda
      digits = digits.replace(/^0+(\d)/, '$1')
      if (digits.length === 1) digits = '0' + digits
      const intPart = digits.slice(0, -2) || '0'
      const decPart = digits.slice(-2)
      const intFmt = intPart.replace(/\B(?=(\d{3})+(?!\d))/g, '.')
      return `${intFmt},${decPart}`
    }
    const handler = () => {
      let s = el.value.replace(/\D/g, '')
      el.value = format(s)
    }
    el.addEventListener('input', handler)
    el.addEventListener('focus', handler)
  }

  maskTwoDecimals(pesoInput)
  maskDecimal(concentracaoInput)

  function parsePtBrNumber(str) {
    if (!str) return NaN
    return Number(str.replace(/\./g,'').replace(',', '.'))
  }

  function showAlert(message) {
    alertBox.textContent = message
    alertBox.classList.remove('hidden')
  }

  form.addEventListener('submit', async (e) => {
    e.preventDefault()
    const submitBtn = form.querySelector('button[type="submit"]')
    const originalText = submitBtn.innerHTML
    
    // Loading state
    submitBtn.disabled = true
    submitBtn.innerHTML = '<span class="inline-block animate-spin mr-2">‚è≥</span> Calculando...'
    
    // Simular pequeno delay para melhor UX (opcional - pode remover se quiser instant√¢neo)
    await new Promise(resolve => setTimeout(resolve, 300))
    
    const categoriaKey = categoriaSelect.value
    const especie = especieSelect.value
    const peso = parsePtBrNumber(pesoInput.value)
    const medOption = medicamentoSelect.options[medicamentoSelect.selectedIndex]
    const medData = medOption?.dataset?.medData ? JSON.parse(medOption.dataset.medData) : null
    const med = medicamentoSelect.value
    const conc = parsePtBrNumber(concentracaoInput.value)
    const sens = racaSensivel.checked
    
    calcular({ 
      categoria: categoriaKey,
      especie, 
      peso, 
      med, 
      medData,
      conc, 
      sens, 
      raw: {pesoStr:pesoInput.value, concStr:concentracaoInput.value, categoria: categoriaKey} 
    })
    
    // Restaurar bot√£o
    submitBtn.disabled = false
    submitBtn.innerHTML = originalText
  })
}

