function calcular({ categoria, especie, peso, med, medData, conc, sens, raw }) {
  const panel = document.getElementById('panel')
  
  // Limpar mensagens de erro anteriores
  const existingAlerts = panel.querySelectorAll('.alert-error')
  existingAlerts.forEach(alert => alert.remove())
  
  const alertMsg = (msg) => {
    const alertEl = document.createElement('div')
    alertEl.className = 'alert-error mt-4 rounded-md border border-red-200 bg-red-50 px-4 py-3 text-sm text-red-700'
    alertEl.textContent = msg
    panel.insertAdjacentElement('beforeend', alertEl)
    // Scroll suave para o erro
    alertEl.scrollIntoView({ behavior: 'smooth', block: 'nearest' })
  }

  if (!Number.isFinite(peso)) return alertMsg('Informe um peso v√°lido (n√∫mero).')
  if (peso <= 0) return alertMsg('Peso deve ser maior que zero.')
  if (peso < limitesPesoKg.min) return alertMsg(`Peso muito baixo. M√≠nimo: ${limitesPesoKg.min} kg.`)
  if (peso > limitesPesoKg.max) return alertMsg(`Peso muito alto. M√°ximo: ${limitesPesoKg.max} kg.`)
  if (!categoria) return alertMsg('Selecione uma categoria de medicamento.')
  if (!med) return alertMsg('Selecione um medicamento.')
  if (!raw.concStr || !Number.isFinite(conc) || conc <= 0) return alertMsg('Concentra√ß√£o √© obrigat√≥ria. Informe um valor em mg/mL maior que 0.')

  let regra = null
  let medNome = med

  // Tentar usar dados do JSON primeiro
  if (medData && medData.Dose_mg_kg) {
    const doseParsed = parseDoseFromJSON(medData.Dose_mg_kg, especie)
    if (doseParsed) {
      medNome = medData.Medicamento || med
      regra = {
        min: doseParsed.min,
        max: doseParsed.max,
        freq: medData.Frequencia || 'Conforme prescri√ß√£o',
        dur: 'Conforme prescri√ß√£o',
        obs: medData.Observacoes || medData.Via || ''
      }
    }
  }

  // Se n√£o encontrou nos JSONs, tentar no DOSAGENS antigo
  if (!regra) {
    regra = getRegra(med, especie)
    if (!regra) return alertMsg('N√£o h√° protocolo cadastrado para esta combina√ß√£o de medicamento e esp√©cie.')
  }

  // Calcular doses m√≠nima, m√°xima e m√©dia
  const doseMinMg = round2(peso * regra.min)
  const doseMaxMg = round2(peso * regra.max)
  const mgKg = regra.min === regra.max ? regra.min : (regra.min + regra.max) / 2
  const doseMgNum = round2(peso * mgKg) // Dose m√©dia calculada
  
  // Calcular volumes em mL
  const volumeMinMl = round2(doseMinMg / conc)
  const volumeMaxMl = round2(doseMaxMg / conc)
  const volumeMlNum = round2(doseMgNum / conc)
  
  // Detectar se √© comprimido baseado na concentra√ß√£o informada e dados do JSON
  const concStr = medData?.Concentracao_mg_mL || ''
  const isComprimido = /comprimido|comp|cp/i.test(concStr) || (conc >= 50 && conc <= 1000 && !/mg\s*\/\s*\d+\s*mL/i.test(concStr))
  
  // Calcular comprimidos (arredondar para 1 casa decimal, permitindo meios comprimidos)
  const comprimidosMin = isComprimido ? round2(doseMinMg / conc) : null
  const comprimidosMax = isComprimido ? round2(doseMaxMg / conc) : null
  const comprimidos = isComprimido ? round2(doseMgNum / conc) : null

  const faixaStr = regra.min === regra.max ? `${regra.min} mg/kg` : `${regra.min}‚Äì${regra.max} mg/kg`
  const alerta = (med.includes('ivermectina') && especie === 'cachorro' && sens)
    ? 'Aten√ß√£o: Ivermectina pode ser contraindicada em ra√ßas sens√≠veis (MDR1).'
    : ''

  renderResultado({
    especie,
    med: medNome,
    peso: peso,
    doseMinMg: formatNumber(doseMinMg),
    doseMaxMg: formatNumber(doseMaxMg),
    doseMg: formatNumber(doseMgNum),
    volumeMinMl: formatNumber(volumeMinMl),
    volumeMaxMl: formatNumber(volumeMaxMl),
    volumeMl: formatNumber(volumeMlNum),
    concentracao: formatNumber(conc),
    comprimidosMin: isComprimido ? formatNumber(comprimidosMin) : null,
    comprimidosMax: isComprimido ? formatNumber(comprimidosMax) : null,
    comprimidos: isComprimido ? formatNumber(comprimidos) : null,
    via: medData?.Via || '',
    freq: regra.freq,
    dur: regra.dur,
    faixaStr,
    doseMinKg: regra.min,
    doseMaxKg: regra.max,
    obs: regra.obs,
    alerta,
    medData: medData || null,
    classe: medData?.Classe || '',
    formState: { especie, pesoStr: raw.pesoStr, categoria: raw.categoria, med, concStr: raw.concStr, sensivel: sens },
  })
}

// Inicializa view
renderForm()


